html {
  box-sizing: border-box;
  font-size: 16px;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

ol,
ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

button {
  padding: 0;
  border: none;
  background: none;
}

/* Main Sections */

.wrapper {
  display: grid;
  grid-template-columns: 20fr 85fr;
  grid-template-rows: 10fr 85fr;
  height: 100vh;
  width: 100vw;
  background-color: #eee;
}

.header {
  background-color: #333;
  grid-column: 1/3;
  grid-row: 1/2;
  display: flex;
  justify-content: start;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
  color: white;
}

.sidebar {
  background-color: #ddd;
  grid-column: 1/2;
  grid-row: 2/3;
}

.sidebar > div {
  width: 80%;
}

.main {
  grid-column: 2/4;
  grid-row: 2/3;
  background-color: #eee;
  width: 40rem;
  margin: 0 auto;
  padding: 2rem;
}

.main h1 {
  text-align: center;
}

#logo {
  height: 100px;
  width: 100px;
}

/*SideBar */

.projects-section {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding: 1rem;
  font-size: 1.5rem;
}

.project {
  display: flex;
}

.left {
  flex: 9;
}

.selected-project {
  background-color: rgb(109, 109, 109);
  border-radius: 0.5rem;
}

.projects div button {
  display: flex;
  font-size: 1.25rem;
  gap: 0.5rem;
  padding: 0.5rem;
}

.project:hover {
  background-color: rgb(109, 109, 109);
  border-radius: 0.5rem;
}

.new-project-button {
  display: flex;
  align-items: center;
  font-size: 1.25rem;
  gap: 0.5rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  margin-bottom: 0.5rem;
}

#new-project-div:hover {
  background-color: rgb(109, 109, 109);
  border-radius: 0.5rem;
}

.projects .project:hover .delete-logo {
  display: block; /* or inline, inline-block, etc. */
}

.delete-logo,
.delete-task-logo {
  display: none;
}

#new-project-form {
  display: none;
  flex-direction: column;
  gap: 1rem;
}

#new-project-buttons-popup {
  display: flex;
  justify-content: space-around;
}

#input-add-project-popup {
  height: 2rem;
  border-radius: 10px;
}

#new-project-buttons-popup > button {
  border-radius: 5px;
  border: 1px solid black;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  font-size: 1rem;
}

#project-add-button-popup {
  background-color: #90ef90;
  padding-right: 2rem;
  padding-left: 2rem;
}

#project-add-button-popup:hover {
  background-color: #8dce8d;
}

#project-cancel-button-popup {
  background-color: #fb8383;
  padding-right: 1rem;
  padding-left: 1rem;
}

#project-cancel-button-popup:hover {
  background-color: #d48888;
}

/* Task section */

.tasks-section {
  display: flex;
  flex-direction: column;
  padding: 1rem;
  font-size: 1.5rem;
}

.task {
  display: flex;
  margin-bottom: 1rem;
}

.left {
  flex: 9;
}

.selected-task {
  background-color: rgb(109, 109, 109);
  border-radius: 0.5rem;
}

.tasks div button {
  display: flex;
  font-size: 1.25rem;
  gap: 0.5rem;
  padding: 0.5rem;
}

.task:hover {
  background-color: rgb(109, 109, 109);
  border-radius: 0.5rem;
}

.new-task-button {
  display: flex;
  align-items: center;
  font-size: 1.25rem;
  gap: 0.5rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  margin-bottom: 0.5rem;
}

.tasks .task:hover .delete-task-logo {
  display: block; /* or inline, inline-block, etc. */
}

#new-task-form {
  display: none;
  flex-direction: column;
  gap: 0.5rem;
}

#new-task-div:hover {
  background-color: rgb(109, 109, 109);
  border-radius: 0.5rem;
}

#new-task-buttons-popup {
  display: flex;
  justify-content: space-around;
}

#input-add-task-popup {
  width: 100%;
  height: 2rem;
  border-radius: 10px;
}

#new-task-buttons-popup {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
}

#new-task-buttons-popup > button {
  flex: 1; /* This will make each button take up 50% of the width */
  border-radius: 5px;
  border: 1px solid black;
  padding: 0.25rem 1rem;
  font-size: 1rem;
  text-align: center; /* This will center the text within the buttons */
}

#new-task-buttons-popup > button {
  border-radius: 5px;
  border: 1px solid black;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  font-size: 1rem;
}

#task-add-button-popup {
  background-color: #90ef90;
  padding-right: 2rem;
  padding-left: 2rem;
}

#task-add-button-popup:hover {
  background-color: #8dce8d;
}

#task-cancel-button-popup {
  background-color: #fb8383;
  padding-right: 1rem;
  padding-left: 1rem;
}

#task-cancel-button-popup:hover {
  background-color: #d48888;
}
